<template>
    <tip/>
</template>

<script>
import { mapMutations } from 'vuex';
import tip from '@/components/tip';

export default {
    name: 'NoBusiness',
    components: { tip },
    methods: {
        ...mapMutations('common', ['changeTipPage']),
        injectData () {
            if (window.localStorage.getItem('noWarnText')) {
                this.changeTipPage({ message: '暂无公安网环境无法办理' });
            } else {
                this.changeTipPage({ message: '该业务暂未开放!' });
            }
        }
    },
    mounted () {
        this.injectData();
    }
};
</script>
